<template>
  <div class="index">
    <div v-for="item in examList" v-bind:key="item.type">{{item.type}}</div>
  </div>
</template>

<script>
const axios = require('axios')
export default {
  name: 'index',
  data () {
    return {
      examList: []
    }
  },
  computed: {
    fullName: function () {
      return this.firstName + this.lastName
    }
  },
  created () {
    var that = this
    axios.get('http://localhost:3000/getExamList', {}).then(function (response) {
      var data = response.data
      that.examList = data
    }).catch(function (error) {
      console.log(error)
    }).finally(function () {
    })

    const asyncReadFile = async function () {
      try {
        const f1 = await that.verify('moon')
        const f2 = await that.verify('kim')
        console.log(f1)
        console.log(f2)
      } catch (error) {
        console.log(error)
      }
    }
    asyncReadFile()
  },
  methods: {
    verify: function (name) {
      return new Promise(function (resolve, reject) {
        axios.post('http://localhost:3000/verifyAccount', {
          account: name
        }).then(function (response) {
          var data = response.data
          if (data.stat === 1) {
            console.log(name, data)
            resolve(data)
          }
        }).catch(function (error) {
          reject(error)
        }).finally(function () {
        })
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .index{
    width:100%;
    height:100%;
  }
</style>



